.discussionBoard{
    display: flex;
    flex-direction: column;
    height: 680px;
}

.discussionHeader{
    display: flex;
    align-items: center;
    height: 60px;
    padding: 20px;
}

.input-postSearch{
    height: 30px;
    margin-right: 10px;
}

.button-create{
    margin-right: 10px;
}


.discussionList{
    margin-bottom:20px;
    padding :0px 20px 0px 20px;
    height: 620px;
    overflow-y: scroll;
}

.discussionList::-webkit-scrollbar {
    display: none;
  }

.serverClosed{
    display: flex;
    justify-content: center;
    font-size: 30px;
    margin-top: 200px;
}