.discussion{
    display: flex;
    flex-direction: column;
    padding:4px;
    border-bottom: 1px solid #cbcddc;
}

.discussion:last-child {
    border-bottom: none;
}

.discussion_info{
    display: flex;
    flex-direction: row;    
    align-items: center;
}

.discussion_avatar{
    border-radius: 50%;
    width: 60px;
    height: 60px;
}

.discussion_titleAndAuthor-Wrapper{
    margin-right :10px;
    margin-left :10px;
    width: 600px

}


.discussion_answerOX{
    width: 20px;
    height: 20px;
}

.discussion_authorAndDate{
    display: flex;
    justify-content: end;
    font-size: 14px;
    opacity: 0.7;
}
