
  

.chatRoom {
    display: flex;
    flex-direction: row;
    position: relative;
    }


.chatRoom button{
    border:none;
}


.chatRoom input{
    border:none;
}

.roomWrapper{
    display: flex;
    flex-direction: column;
    margin:10px 10px 10px 0px;
    width: 230px;
    height: 600px;
    background-color: white;
    border-radius: 10px;
}


.roomInterior{
    margin:10px 10px 10px 0px;
    width: 400px;
    height: 600px;
    background-color: white;
    border-radius: 10px;
}

.chatList{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    overflow:auto;
    list-style: none;
    margin: 20px 20px 0px 20px;
    height: 470px;
    background-color: #e8ecf9;
    border-radius: 8px;
}

.chatList::-webkit-scrollbar {
    display: none;
}

textarea {
    padding: 8px;
    resize: none;
}

textarea:focus {

    outline: none;
    box-shadow: none;
}

textarea::-webkit-scrollbar {
    display: none;
}

input {
    padding: 8px;
}

input:focus {
    outline: none;
    box-shadow: none;
}

.inputArea{
    display: flex;
    height: 130px;
    
}
.input-message{
    
    border: none;
    margin: 20px 20px 20px 20px;
    height: 70px;
    flex: 1 0 auto;
    background-color: rgb(248,248,248);
    border-radius: 10px;
}   

.button-message{
    
    width: 70px;
    height: 70px;
    margin: 20px 20px 20px 0px;
    border-radius: 6px;
    background-color: rgb(203, 213, 233);
}

.modify-username{
    display: flex;
    flex-direction: row;
}

.input-username{
    flex: 1 0 auto;
    margin:10px 10px 0px 10px;
    height: 30px;
    border-radius: 6px;
    background-color: rgb(203, 213, 233);
}

.roomList{
    overflow: auto;
    list-style:none;
    margin:20px;
    padding-bottom: 10px;
    height: 470px;
}

/* ( 크롬, 사파리, 오페라, 엣지 ) 동작 */
.roomList::-webkit-scrollbar {
    display: none;
  }

.room{
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    padding: 10px;
    height: 50px;
    background-color: rgb(248, 248, 248);
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    border-radius: 10px;
}



.chatBox{
    display: flex;
    flex-direction: column;
    
}

.chatBox2{
    display: flex;
    flex-direction: column;
}

.chatBoxInfo{
    font-weight: 600;
    color:rgb(59, 59, 174);
    margin: 4px 4px 4px 8px;
}

.chatBoxText{
    display: inline-block;
    background-color: white;
    padding: 6px;
    border-radius: 6px;
    width: fit-content;
    margin-left: 10px;
    margin-bottom: 6px;
    margin-right: 10px;
    white-space: pre-wrap;
    word-break: break-all;
}

.button-roomMake{
    flex: 0 1 auto;
    margin: 0px 10px 10px 10px;
    height: 60px;
    border-radius: 6px;
}

.roomMakeBox{
    position:absolute;
    top: 30%;
    left: 50%;
    width: 400px;
    height: 80px;
    
    display: flex;
    flex-direction: column;

    border-radius: 10px;
    background-color: rgb(231, 231, 238);
    transform: translate(-50%, -50%);
}

.input-roomname{
    margin: 10px 10px 0px 10px;
    border-style: none;
    border-radius: 6px;
    width: 380px;
    height : 40px;
}


.roomMakeButtonArea{
    margin-top: 5px;
    display:flex;
    justify-content: flex-end;
}

.button-roomMakeYes{
    width: 20px;
    height: 20px;
    margin-right: 6px;
    background-color:transparent;
}

.button-roomMakeClose{
    margin-right: 10px;
    width: 20px;
    height: 20px;
    background-color:transparent;
}


.hide{
    display: none;
}
