*{
  box-sizing: border-box;
  font-family: 'Noto Sans KR', sans-serif;
  margin: 0px;
  padding: 0px;
  word-wrap: break-word; 
}

pre {
  white-space: pre-wrap;
}

body{
  background-color: #cecee6; 
}

li{
  list-style-type: none;
}

header{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 70px;
  background-color: #191c3b;
  color: white;
  font-size: 30px;
}