.discussionDetail{
    /* transition: height 2s ; */
    margin:10px;
    background-color: #e8ecf9
}

.discussionDetail > * {

    margin-left: 20px;
    margin-right: 20px;
}

.discuss-input{
    display: flex;
    flex-direction: column;
}

.discuss-input > *{
    margin:10px;
}


.discuss-textarea{
    height: 200px;
}

.discuss-username-input{
    height: 30px;
    width: 120px;
}

.discuss-submit-button{
    height: 30px;
    width: 80px;
}






.discuss-question{
    padding: 20px;
    border-bottom: 2px solid #cbcddc;
}


.discuss-answer-text{
    padding:0px 20px 20px 20px  
}

.disscuss-answer-info{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: end;
    padding:20px;
}

.disscuss-answer-avatar{
    border-radius: 50%;
    width: 60px;
    height: 60px;
    
}

.disscuss-answer-authorAndDate{
    font-size: 14px;
    opacity: 0.7;
}




